.labelContainer {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
.break-label {
  padding: 10px;
}
.session-label {
  padding: 10px;
}

#break-decrement #break-increment {
  display: flex;
  flex-direction: column;
  justify-self: flex-end;
  justify-self: flex-start;
}
#break-label {
  display: flex;
  flex-direction: row;
}
#session-label {
  display: flex;
  align-self: flex-start;
}
#iconBdown {
  position: absolute;
  top: 35%;
  left: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 30px;
  display: block;
  line-height: 50%;
  align-content: center;
}

#iconSdown {
  position: absolute;
  top: 35%;
  left: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 30px;
  display: block;
  line-height: 50%;
  align-content: center;
}
#iconBup {
  position: absolute;
  top: 65%;
  left: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 30px;
  display: block;
  line-height: 50%;
  align-content: center;
}
#iconSup {
  position: absolute;
  top: 65%;
  left: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 30px;
  display: block;
  line-height: 50%;
  align-content: center;
}

h2 {
  font-size: 22px;
  text-transform: lowercase;
  color: #585858;
  font-family: "Open Sans", sans-serif;
}

#break-length {
  font-family: "Open Sans", sans-serif;
  text-transform: lowercase;
  color: #585858;
}
#session-length {
  font-family: "Open Sans", sans-serif;
  text-transform: lowercase;
  color: #585858;
}
#time-left {
  font-family: "Open Sans", sans-serif;
  color: rgb(43, 41, 42);
}
#mainLabel {
  text-transform: lowercase;
}

.timer {
  font-size: 38px;
  border: 6px solid rgb(250, 179, 158);
  border-radius: 7px;
  width: 160px;
  height: 70px;
  margin: 20px auto 10px auto;
  text-align: center;
  align-self: flex-end;
}

.start {
  padding: 8px;
  font-size: 15px;
}
.reset {
  padding: 8px;
  font-size: 15px;
}
